import React from "react";

const App = ({ name, id, email }) => {
  return (
    <div className="tc ma3 pa3 bg-light-blue dib br3 grow bw2 shadow-5">
      <img src={`https://robohash.org/${id}?set=set4`} alt="cat" />
      <div>
        <h3>{name}</h3>
        <p>{email}</p>
      </div>
    </div>
  );
};

export default App;
