import React from "react";
import Card from "./Card";

const Arraylist = ({ cats }) => {
  return (
    <div>
      {cats.map((user, i) => {
        return (
          <Card key={i} name={user.name} id={user.id} email={user.email} />
        );
      })}
    </div>
  );
};

export default Arraylist;
